<template>
  <div class="search">
    <Search placeholder="输入关键词搜索商品" @search="search" />
    <InfiniteList ref="list" v-slot="slotProps" api="teamManage/searchGoods" :params="{goods_name}">
      <div class="list">
        <div
          class="list-item flex ac"
          v-for="item in slotProps.list"
          :key="item.goods_id"
          @click="toDetail(item.goods_id, item.goods_name)"
        >
          <CustomImg width="1.6rem" height="1.6rem" :src="item.original_img" />
          <div class="item goods-name">{{item.goods_name}}</div>
          <img class="icon" :src="require('@/assets/right.png')" alt />
        </div>
      </div>
    </InfiniteList>
  </div>
</template>

<script>
import Search from "@/components/Search";
import InfiniteList from "@/components/InfiniteList";

export default {
  components: { Search, InfiniteList },
  data() {
    return {
      goods_name: ""
    };
  },
  methods: {
    // 搜索
    search(goods_name) {
      this.goods_name = goods_name;
      this.$refs.list.resetData();
    },
    // 详情
    toDetail(goods_id, goods_name) {
      const params = {
        goods_id,
        goods_name: goods_name.replace(/%/g, '%25'),
        isSearch: true,
      };
      let str = "";
      for (let i in params) {
        str += `&${i}=${params[i]}`;
      }
      str = str.slice(1);
      this.$router.push(`/goods/detail?${str}`);
    }
  }
};
</script>

<style lang="less" scoped>
.search {
  min-height: 100vh;
}

.list-item {
  width: 349px;
  height: 85px;
  background: rgba(255, 255, 255, 1);
  border-radius: 6px;
  margin: 0 auto 10px;
  background: #fff;
  padding: 0 10px;
  box-sizing: border-box;
}

.icon {
  width: 6px;
}

.goods-name {
  padding: 0 10px;
  color: #252525;
  font-size: 14px;
  line-height: 20px;
}
</style>